<template>
  <div class="EditFinancial">
      <div class="title">
          <p :class="{ 'dark-mode-title': getDarkMode }">update financial</p>
      </div>
      <div :class="{ 'dark-moode': getDarkMode }" class="allContentt">
      <div class="formbold-main-wrapper">
          <div class="formbold-form-wrapper">
            <FormImage></FormImage>                
            <ValidationObserver ref="observer" v-slot="{ invalid }">
              <form @submit.prevent="HandelupdateTransaction">
              <div class="formbold-input-flex">
                <div>
                  <label for="order" class="formbold-form-label"> </label>
                  <ValidationProvider name=" " rules="numeric|min_value:1" v-slot="{ errors }">
                    <input list="orderList" class="formbold-form-input" placeholder="  " v-model="selectedOrder" @input="updateOrderId">
                    <datalist id="orderList">
                      <option v-for="order in getUserOrders" :key="order.id" :value="order.numberOfOrder"></option>
                    </datalist>
                    <span class="error">{{ errors[0] }}</span>
                  </ValidationProvider>
                </div>
                  
                <div>
                  <ValidationProvider name=" " rules="numeric|min_value:1" v-slot="{ errors }">
                  <label for="amount" class="formbold-form-label"> </label>
                  <input type="number" class="formbold-form-input" placeholder=" " v-model="amount">
                  <span class="error">{{ errors[0] }}</span>
                  </ValidationProvider>
                </div>

              </div>

              <div class="formbold-input-flex">
                  <div>
                    <label for="addedPayments" class="formbold-form-label">  </label>
                    <select class="formbold-form-input" v-model="selectedPaymentId" id="addedPayments" @change="selectPaymentForUpdate" placeholder="   ">
                      <option v-for="payment in payments" :value="payment.id" :key="payment.id">
                        {{ payment.amount }} 
                      </option>
                    </select>
                  </div>

                  <div>
                    <label for="status" class="formbold-form-label"> </label>
                    <ValidationProvider name=" " rules="required" v-slot="{ errors }">
                      <select class="formbold-form-input" v-model="status">
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                      <span class="error">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
              </div>

              <div class="formbold-input-flex">
                  <div>
                    <label for="currentAmount" class="formbold-form-label"></label>
                    <ValidationProvider name=""  :rules="`${payments.length == 0 ? 'required|numeric|min_value:1' : ''}`"  v-slot="{ errors }">
                      <input type="number" class="formbold-form-input" placeholder=" " id="currentAmount" v-model="currentAmount">
                      <span class="error">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>

                  <div>
                    <label for="currentTransactionDate" class="formbold-form-label">  </label>
                    <ValidationProvider name="  " :rules="`${payments.length == 0  ? 'required|date_format:YYYY-MM-DD' : ''}`" v-slot="{ errors }">
                      <input type="date" class="formbold-form-input" id="currentTransactionDate" v-model="currentTransactionDate">
                      <span class="error">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
              </div>


              <div class="formbold-mb-3 cont_add_del_upda">
              <button type="button"  class="addProduct-btn" @click="addPayment"> </button>
              <button type="button" class="updateProduct-btn" @click="updatePayment"> </button>
              <button class="deleteProduct-btn" type="button" @click="deletePayment"> </button>

              </div>

              <div class="formbold-mb-3">
                <label for="dob" class="formbold-form-label"><span>(     )</span>   </label>
                <ValidationProvider name=" "    :rules="`${!selectedOrder ? 'required|date_format:YYYY-MM-DD' : ''}`"  v-slot="{ errors }">
                  <input type="date" name="dob" id="dob" class="formbold-form-input" v-model="date">
                  <span class="error">{{ errors[0] }}</span>
                </ValidationProvider>
              </div>

              <div class="formbold-mb-3">
                <label for="address" class="formbold-form-label"><span>(    )</span>   </label>
                <ValidationProvider name="  " :rules="`${!selectedOrder ? 'required' : ''}`"  v-slot="{ errors }">
                  <input type="text" id="address" placeholder="" class="formbold-form-input formbold-mb-3" v-model="adress">
                  <span class="error">{{ errors[0] }}</span>
                </ValidationProvider>
              </div>

              <div class="formbold-mb-3">
                  <label for="message" class="formbold-form-label">
                  
                  </label>
                  <textarea
                  rows="6"
                  id="message"
                  class="formbold-form-input"
                  v-model="notes"

                  ></textarea>
              </div>


              <button  class="formbold-btn" :class="{ 'disabled-btn': invalid }" :disabled="invalid">   </button>
                 
             </form>
          </ValidationObserver>

          </div>
          </div>
    </div>
    <CircleLoader :show="isLoading" />

  </div>
</template>

  
<script>

import { extend } from 'vee-validate';
import { required , numeric , min_value , required_if } from 'vee-validate/dist/rules';

// Register rules with custom messages

extend('required', {
...required,
message: '{_field_} '
});

extend('numeric', {
...numeric,
message: '{_field_}    '
});

extend('min_value', {
...min_value,
message: '{_field_}      {min}'
});

extend('required_if', {
...required_if,
message: '         .',
});

extend('date_format', {
validate(value) {
  const regex = new RegExp('^\\d{4}-\\d{2}-\\d{2}$')
  return regex.test(value);
},
params: ['format'],
message: '{_field_}      {format}'
});


// FormImage
import FormImage from "@/shared/components/svgImage/FormImage.vue";

// CircleLoader
import CircleLoader from '@/shared/components/loading/CircleLoader.vue';

// mapActions , mapState
import { mapActions, mapState } from 'pinia';

// store
import { useTransactionsStore } from '@/store/transactions/transactions.js';
import { useOrdersStore } from '@/store/order/orders.js';
import { useGetUserStore } from '@/store/users/users.js';

// to make id for payment : use in (1) for-loop to show them (2) select to update
import { v4 as uuidv4 } from 'uuid';

export default {
  components: {
      FormImage,
      CircleLoader,
    },
  data() {
    return {
      // get info from order if chossed
      selectedOrder: '',
      
      // info for transaction
      adress: '',
      date: '',
      orderId: null,
      numberOfOrder: '',
      amount: null,
      status: '',
      notes: '',

      // for add transaction
      payments: [],
      currentAmount: '',
      currentTransactionDate: '',

      // need for handeling
      isEditing: false,
      selectedPaymentId: null,
      unregisteredOrderMessage: '',
      userInfo: null,

      // transaction Id
      transactionId:'', 
      // loading 
      isLoading: false

    };
  },
  computed: {
    ...mapState(useOrdersStore, ['orders']),
     getDarkMode() {
      return this.$store.state.darkMode;
    },
    getUserOrders() {
          let filteredOrders = this.orders.filter(order => order.status !== '0');
          if (this.$route.params.profileId ) {
              filteredOrders = filteredOrders.filter(order => 
              order.customerId ===this.$route.params.profileId || order.technicalId === this.$route.params.profileId
            );
          } 
     return filteredOrders;

    } 
  },
  created() {      
      
      this.fetchOrders();
      const userId = this.$route.params.profileId;
      this.userInfo = this.fetchSingleUser(userId);
      
      this.transactionId = this.$route.params.transactionId
      this.loadTransaction(this.transactionId);

    

  },
  methods: {
    ...mapActions(useOrdersStore, ['fetchOrders','updateOrder']),
    ...mapActions(useTransactionsStore, ['updateTransaction','fetchTransactionById']),
    ...mapActions(useGetUserStore, ['fetchSingleUser']),


    async loadTransaction(transactionId) {
      const transaction = await this.fetchTransactionById(transactionId);
      if (transaction) {
        this.transactionId = transaction.id;
        this.orderId = transaction.orderId;
        this.numberOfOrder = transaction.numberOfOrder;
        this.selectedOrder = transaction.numberOfOrder;
        this.adress = transaction.adress;
        this.date = transaction.date;
        this.amount = transaction.amount;
        this.status = transaction.status;
        this.notes = transaction.notes;
        this.payments = transaction.payments;
      }
      console.log(this.selectedOrder , this.numberOfOrder)
    },
    submit(){
    console.log('Fetched user info id:', this.userInfo.id);   
    },
    updateOrderId() {
      const selectedOrderObj = this.orders.find(order => order.id === this.selectedOrder);
      if (selectedOrderObj) {
        this.orderId = selectedOrderObj.id;
        this.adress = selectedOrderObj.adress;
        this.date = selectedOrderObj.date;
        this.numberOfOrder = selectedOrderObj.numberOfOrder;
        this.unregisteredOrderMessage = '';
      } else {
        this.orderId = null;
        this.unregisteredOrderMessage = '     .';
      }
    },

    addPayment() {
      console.log("before payment",this.currentAmount , this.currentTransactionDate)
      console.log(" all payment",this.payments )
      if (this.currentAmount && this.currentTransactionDate) {
        this.payments.push({
          id: uuidv4(),
          amount: this.currentAmount,
          transactionDate: this.currentTransactionDate,
        });
        this.clearPaymentForm();
        console.log("added payment")
      }
      console.log("after payment",this.currentAmount , this.currentTransactionDate)
      console.log(" all payment",this.payments )
    },
    toggleButtons(isEditing) {
      const addButton = document.querySelector('.addProduct-btn');
      const updateButton = document.querySelector('.updateProduct-btn');
      const deleteButton = document.querySelector('.deleteProduct-btn');

      if (isEditing) {
        addButton.classList.add('addProduct-btn_hidde');
        updateButton.classList.add('updateProduct-btn_show');
        deleteButton.classList.add('deleteProduct-btn_show');
      } else {
        addButton.classList.remove('addProduct-btn_hidde');
        updateButton.classList.remove('updateProduct-btn_show');
        deleteButton.classList.remove('deleteProduct-btn_show');
      }
    },

    selectPaymentForUpdate() {
      const selectedPayment = this.payments.find(payment => payment.id === this.selectedPaymentId);
      if (selectedPayment) {
        this.currentAmount = selectedPayment.amount;
        this.currentTransactionDate = selectedPayment.transactionDate;
        this.isEditing = true;
        this.toggleButtons(true); 
      }
    },

    updatePayment() {
      const index = this.payments.findIndex(payment => payment.id === this.selectedPaymentId);
      if (index !== -1) {
        this.payments[index].amount = this.currentAmount;
        this.payments[index].transactionDate = this.currentTransactionDate;
        this.clearPaymentForm();
        
      }
    },

    deletePayment() {
      const index = this.payments.findIndex(payment => payment.id === this.selectedPaymentId);
      if (index !== -1) {
        this.payments.splice(index, 1);
        this.clearPaymentForm();
      }
    },

    clearPaymentForm() {
      this.currentAmount = '';
      this.currentTransactionDate = '';
      this.selectedPaymentId = null;
      this.isEditing = false;
      this.toggleButtons(false);
    },

    async HandelupdateTransaction() {

    
    const userId = this.$route.params.profileId;
    this.userInfo = await this.fetchSingleUser(userId);


    try {
      this.isLoading = true;

      const updateTransaction = {
        id: this.transactionId,
        orderId: this.orderId,
        numberOfOrder: this.numberOfOrder,
        status: this.status,
        adress: this.adress,
        date: this.date,
        amount: this.amount,
        notes: this.notes,
        payments: this.payments,
        userId: this.userInfo.id,
        transactionType: this.userInfo.role === 'Technical' ? '' : '',
      };

      await this.updateTransaction(updateTransaction);

      if (this.orderId) {
        const updatePayload = {
          id: this.orderId,
          ...(this.userInfo.role === 'Technical'
            ? { financialTechnicalTransactionId: this.transactionId }
            : { financialClientTransactionId: this.transactionId }),
        };

        await this.updateOrder(updatePayload);
      }
      this.isLoading = false;

      this.$router.push({ name: 'AdminTransactionDetails', params: { transactionId: this.transactionId } });
    } catch (error) {
      this.isLoading = false;
      console.error('Error creating transaction:', error);
    }
         }
  },
};

</script>


<style scoped lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
  .EditFinancial {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 13px;
   // background-color: aqua;
  }
  .title {
    width: 470px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    p {
      text-transform: capitalize;
      font-size: 22px;
      font-weight: 600;
    }
  }
  
.allContentt {
    width: 670px;
    display: flex;
    flex-wrap: wrap;
    min-height: 100vh;
    background-color: white;
    border-radius: 10px;
    border: solid 1px rgb(181, 179, 179);
  }


  // phone
  @media (max-width: 477px) {
    .title {
      p {
        margin-left: 60px;
      }
    }
    .allContentt {
      width: 370px !important;
    }
    .formbold-img {
      width:100% !important;
        
   }

   .formbold-main-wrapper {
     padding: 0px !important;
    }
  }
  
  </style>
  