<template>
    <div class="addOrder">
        <div class="title">
            <p :class="{ 'dark-mode-title': getDarkMode }">create order</p>
        </div>
        <div :class="{ 'dark-moode': getDarkMode }" class="allContentt">
        <div class="formbold-main-wrapper">
            <div class="formbold-form-wrapper">
              <FormImage></FormImage>
              <ValidationObserver ref="observer" v-slot="{ invalid }">
              <form @submit.prevent="creatNewOrder">
                <div class="formbold-input-flex">
                    <div>
                        <label for="customer" class="formbold-form-label"> </label>
                        <input list="customerList"  class="formbold-form-input"  placeholder="  " v-model="selectedCustomer" @input="updateCustomerId">
                        <datalist id="customerList">
                        <option v-for="customer in clientUsers" :key="customer.id" :value="customer.name" ></option>
                        </datalist>
                        <p v-if="unregisteredCustomerMessage" class="warning-message">{{ unregisteredCustomerMessage }}</p>
                      </div>
                    
                    <div>
                        <label for="technical" class="formbold-form-label"> :  </label>
                        <input list="technicalList"  class="formbold-form-input"   placeholder="  " v-model="selectedTechnical" @input="updateTechnicalId">
                        <datalist id="technicalList">
                        <option v-for="tech in technicalUsers" :key="tech.id" :value="tech.name" > </option>
                        </datalist>
                        <p v-if="unregisteredTechnicalMessage" class="warning-message"> {{ unregisteredTechnicalMessage }} </p>
                    </div>
                </div>

                <div class="formbold-input-flex">
                    <div>
                      <label for="addedOrders" class="formbold-form-label">  </label>
                      <select
                        class="formbold-form-input"
                        v-model="selectProductForUpdateProp"
                        id="addedOrders"
                        @change="selectProductForUpdate"
                          placeholder="     "
                      >
                        <option v-for="product in addedOrders" :value="product.id" :key="product.id">
                          {{ product.name }}
                        </option>
                      </select>
                    </div>

                    <div>
                      <ValidationProvider name=" " rules="required" v-slot="{ errors }">

                        <label class="formbold-form-label"> </label>
                        <select class="formbold-form-input" name="occupation" id="occupation" v-model="invoiceType">
                        <option value=""></option>
                        <option value=""> </option>
                        <option value=" ">  </option>
                        </select>
                        <span class="error">{{ errors[0] }}</span>
                      </ValidationProvider>
                    </div>
                </div>

                <div class="formbold-input-flex">
                    
                    <div>
                      
                      <label for="product" class="formbold-form-label">    </label>
                      <ValidationProvider name=""  :rules="`${addedOrders.length == 0 ? 'required' : ''}`"  v-slot="{ errors }">
                        <input list="propList"  class="formbold-form-input"  placeholder="   " v-model="selectedProduct" @input="updateProductId">
                        <datalist id="propList">
                          <option v-for="product in myAllProducts" :key="product.id" :value="product.name" ></option>
                        </datalist>
                        <span class="error">{{ errors[0] }}</span>
                      </ValidationProvider>
                    </div>


                    <div>
                      
                      <ValidationProvider name=" " rules="required" v-slot="{ errors }">
                        <label for="status" class="formbold-form-label">   </label>
                        <select
                          id="status"
                          placeholder=" "
                          class="formbold-form-input"
                          v-model="status">
                          <option value="0"></option>
                          <option value="1"></option>
                          <option value="3"></option>
                        </select>
                        <span class="error">{{ errors[0] }}</span>
                      </ValidationProvider>
                    </div>
                </div>

                <div class="formbold-input-flex">
                    <div>
                    <label for="priceOffer" class="formbold-form-label">   </label>
                    <input
                        type="number"
                        id="priceOffer"
                        placeholder=" "
                        class="formbold-form-input"
                        v-model="price_offer"
                    />
                    </div>

                    <div>
                      <ValidationProvider name=""  :rules="`${addedOrders.length == 0 ? 'required|numeric|min_value:1' : ''}`"  v-slot="{ errors }">
                        <label for="quantity" class="formbold-form-label">   </label>

                        <input
                            type="number"
                            id="quantity"
                            placeholder=""
                            class="formbold-form-input"
                            v-model="quantity"
                        
                        />
                        <span class="error">{{ errors[0] }}</span>
                      </ValidationProvider>
                    </div>
                </div>

                <div class="formbold-mb-3 cont_add_del_upda">
                  <button @click.prevent="addProduct" :disabled="!selectedProduct || !quantity" class="addProduct-btn"> </button>
                  <button @click.prevent="updateProduct" :disabled="!selectedProduct || !quantity"  class="updateProduct-btn" >   </button>
                  <button @click.prevent="deleteProduct" class="deleteProduct-btn">  </button>
                </div>
                <div class="formbold-input-flex">
                    
                    <div>
                      <ValidationProvider name=" " rules="numeric|min_value:1" v-slot="{ errors }">
                        <label for="numberOfOrders" class="formbold-form-label">     </label>
                        <input
                        type="number"
                        id="numberOfOrders"
                        placeholder=" "
                        class="formbold-form-input"
                        v-model="number"
                    
                       />
                        <p  class="info-message">       </p>
                        <span class="error">{{ errors[0] }}</span>
                      </ValidationProvider>
                    </div>


                    <div>
                      <label for="laborPrice" class="formbold-form-label">    </label>
                      <input
                        type="number"
                        id="laborPrice"
                        placeholder="   "
                        class="formbold-form-input"
                        v-model="laborPrice"
                        
                        />
                        <p  class="info-message">                 </p>
                        
                      </div>
                    </div>
                    
                    <div class="formbold-mb-3">
                      <ValidationProvider name="  " rules="date_format:YYYY-MM-DD" v-slot="{ errors }">
                        <label for="dob" class="formbold-form-label">     </label>
                        <input type="date" name="dob" id="dob" class="formbold-form-input"  v-model="date" />
                        <span class="error">{{ errors[0] }}</span>
                      </ValidationProvider>
                    </div>
                    
                    <div class="formbold-mb-3">
                      <label for="address" class="formbold-form-label">   </label>
                      <input
                      type="text"
                      id="address"
                      placeholder=""
                      class="formbold-form-input formbold-mb-3"
                      v-model="adress"
                      />
                    </div>

                <div class="formbold-mb-3">
                    <label for="message" class="formbold-form-label">
                    
                    </label>
                    <textarea
                    rows="6"
                    id="message"
                    class="formbold-form-input"
                    v-model="notes"

                    ></textarea>
                </div>

                <div class="formbold-form-file-flex">
                    <label for="upload" class="formbold-form-label">
                    Upload disign
                    </label>
                    <input
                    type="file"
                    id="upload"
                    class="formbold-form-file"
                    @change="handleFileUpload" 
                    />
                </div> 

                <button class="formbold-btn" :class="{ 'disabled-btn': invalid }" :disabled="invalid"  >   </button>
                   
               </form>
              </ValidationObserver>

            </div>
            </div>
      </div>
      <CircleLoader :show="isLoading" />

    </div>
  </template>
  <script>
  
  import { extend } from 'vee-validate';
  import { required , numeric , min_value  } from 'vee-validate/dist/rules';

  // Register rules with custom messages

  extend('required', {
  ...required,
  message: '{_field_} '
  
  });

  extend('numeric', {
  ...numeric,
  message: '{_field_}    '
  });

  extend('min_value', {
  ...min_value,
  message: '{_field_}      {min}'
  });

  extend('date_format', {
  validate(value) {
    const regex = new RegExp('^\\d{4}-\\d{2}-\\d{2}$')
    return regex.test(value);
  },
  params: ['format'],
  message: '{_field_}      {format}'
  });

  // actions , states
  import { mapActions , mapState } from 'pinia'
  
// components  

  // FormImage
  import FormImage from "@/shared/components/svgImage/FormImage.vue";
  
  // CircleLoader
  import CircleLoader from '@/shared/components/loading/CircleLoader.vue';

  //store
  import { useProductsStore } from '@/store/products/products.js'
  import { useCategoriesStore } from '@/store/categories/categories.js';
  import { useGetUserStore } from '@/store/users/users.js';
  import { useOrdersStore } from '@/store/order/orders.js';

  // sweetalert 
  import sweetalert from "sweetalert";

  export default {
    name: "AddNewOrder",
    components: {
      FormImage,
      CircleLoader,
    },
    data() {
        return {
        // for all products added  
           addedOrders: [],

        // product data  
          selectedProduct:'',
          productId:'',
          price_offer: '',
          quantity: '',
          //itemName:'',
          productInfo:'',

        // rest of order data    
          selectedCustomer:'',
          customerId:null,
          customerInfo:'',

          selectedTechnical:'',
          TechnicalId:null,
          TechnicaInfo:'',
          
          invoiceType: " ",
          date: '',
          adress: '',
          notes: '',
          status:'0', //    
          number:'',
          laborPrice:'',

          // selectProductForUpdate
          selectProductForUpdateProp:'',

          // follow edit state
          isEditingProduct: false, 

          // message when there is no users with this name
          unregisteredCustomerMessage: '', 
          unregisteredTechnicalMessage: '', 
        
          file: null, // for add picture

          // loading 
          isLoading: false


        };
    },
    computed: {
      getDarkMode() {
        return this.$store.state.darkMode;
      },
  
      
      ...mapState(useProductsStore, {
        myAllProducts: 'products',
    
    }),
      ...mapState(useCategoriesStore, ['categories']),
      ...mapState(useGetUserStore, ['technicalUsers','clientUsers']),
  
      
  
    },
    async created() {
      this.fetchCategories();
      this.fetchProducts()
      this.fetchUsers()
  
    },
    methods: {
      // ============ my actions => start =============================================
  
      ...mapActions(useProductsStore, ['fetchProducts']),
      ...mapActions(useCategoriesStore, ['fetchCategories']),
      ...mapActions(useGetUserStore, ['fetchUsers']),
      ...mapActions(useOrdersStore, ['addOrder','uploadImage','generateOrderNumber']),

  
      // ============ my actions => end ==============================================

      // ============ handel file  => start ==============================================
        handleFileUpload(event) {
        this.file = event.target.files[0];
      },
      // ============ handel file => end ==============================================


      // ============ FileReader  for show the selected image localy before upload => start =============================================
      // ============ FileReader for show the selected image localy before upload => end ==============================================
  
      click(el) {
        document.getElementById("inputField").click();
        el.preventDefault();
      },
  
      addProduct() {
      if (!this.isEditingProduct) {
        this.addedOrders.push({
          id: this.productId,
          name: this.selectedProduct,
          price_offer: this.price_offer,
          quantity: this.quantity,
          productInfo:this.productInfo,
        });
        this.clearProductForm();
      }
     },
     clearProductForm() {
      this.selectedProduct = '';
      this.productId = '';
      this.quantity = '';
      //this.itemName = '';
      this.selectProductForUpdateProp = '';
      this.isEditingProduct = false; 
      this.toggleButtons(false);
    },
    selectProductForUpdate() {
      const selectedUpdatePro = this.addedOrders.find(product => product.id === this.selectProductForUpdateProp);
      if (selectedUpdatePro) {
        this.selectedProduct = selectedUpdatePro.name;
        this.productId = selectedUpdatePro.id;
        this.price_offer = selectedUpdatePro.price_offer;
        this.quantity = selectedUpdatePro.quantity;
        //this.itemName = selectedUpdatePro.nameCategory;
        this.isEditingProduct = true;
        this.toggleButtons(true); 
      }
    },
    updateProduct() {
      const index = this.addedOrders.findIndex(product => product.id === this.productId);
      if (index !== -1) {
        this.addedOrders[index].name = this.selectedProduct;
        this.addedOrders[index].price_offer = this.price_offer;
        this.addedOrders[index].quantity = this.quantity;
       // this.addedOrders[index].nameCategory = this.itemName;
        this.clearProductForm();
      }
    },
    deleteProduct() {
      const index = this.addedOrders.findIndex(product => product.id === this.productId);
      if (index !== -1) {
        this.addedOrders.splice(index, 1);
        this.clearProductForm();
      }
    },
    updateProductId() {
      const selectedProductObj = this.myAllProducts.find(product => product.name === this.selectedProduct);
      this.productId = selectedProductObj ? selectedProductObj.id : '';
      this.productInfo = selectedProductObj ? selectedProductObj : '';

    },
    updateCustomerId() {
      const selectedCustomerObj = this.clientUsers.find(customer => customer.name === this.selectedCustomer);
      if (selectedCustomerObj) {
        this.customerId = selectedCustomerObj.id;
        this.customerInfo = selectedCustomerObj;
        
        this.unregisteredCustomerMessage = ''; 
      } else {
        this.customerId = null;
        this.customerInfo = '';

        this.unregisteredCustomerMessage = '     .    .';
      }
    },
    updateTechnicalId() {
      const selectedTechnicalObj = this.technicalUsers.find(tech => tech.name === this.selectedTechnical);
      if (selectedTechnicalObj) {
        this.TechnicalId = selectedTechnicalObj.id;
        this.TechnicaInfo = selectedTechnicalObj;
        
        this.unregisteredTechnicalMessage = ''; 
      } else {
        this.TechnicalId = null;
        this.TechnicaInfo = '';

        this.unregisteredTechnicalMessage = '     .    .';
      }
    },
    toggleButtons(isEditing) {
      const addButton = document.querySelector('.addProduct-btn');
      const updateButton = document.querySelector('.updateProduct-btn');
      const deleteButton = document.querySelector('.deleteProduct-btn');

      if (isEditing) {
        addButton.classList.add('addProduct-btn_hidde');
        updateButton.classList.add('updateProduct-btn_show');
        deleteButton.classList.add('deleteProduct-btn_show');
      } else {
        addButton.classList.remove('addProduct-btn_hidde');
        updateButton.classList.remove('updateProduct-btn_show');
        deleteButton.classList.remove('deleteProduct-btn_show');
      }
    },
    
    // ============ creat New oredr => start =====================================

    async creatNewOrder() {
      this.isLoading = true;
      
      try {
        if (this.addedOrders.length === 0) {
        sweetalert("     .", "     .", "error");
        this.isLoading = false;
        return;
      }

      //const totalPrice = this.addedOrders.reduce((total, product) => total + parseFloat(product.price_offer) * parseInt(product.quantity), 0);

      let imageUrl = '';

      if (this.file) {
        imageUrl = await this.uploadImage({ file: this.file });
      }
       if (!this.number) {
        this.number = await this.generateOrderNumber();
      }

      const newOrder = {
        products: this.addedOrders,
        customerName: this.selectedCustomer,
        customerId: this.customerId,
        customerInfo: this.customerInfo,
        technicalName: this.selectedTechnical,
        technicalId: this.TechnicalId,
        TechnicaInfo:this.TechnicaInfo,
        invoiceType: this.invoiceType,
        adress: this.adress,
        date: this.date,
        status: this.status,
        notes: this.notes,
        numberOfOrder: this.number,
        laborPrice: this.laborPrice,
        imageUrl,
      };
      console.log('before send');

      const orderId = await this.addOrder(newOrder);

      console.log('after send');
      this.isLoading = false;
      sweetalert("   !", "     .", "success");

      this.$router.push({ name: 'Fatora', params: { orderId: orderId } });
    } catch (error) {
      console.error('Error creating order:', error);
      this.isLoading = false;
      sweetalert("   ", "   .", "error");
    }
  },
    // ============ creat New oredr => end =======================================
    
  }
  };
  </script>
  <style scoped lang="scss">
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
  .addOrder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 13px;
    //background-color: aqua;
  }
  .title {
    width: 470px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    p {
      text-transform: capitalize;
      font-size: 22px;
      font-weight: 600;
    }
  }
  



.allContentt {
    width: 670px;
    display: flex;
    flex-wrap: wrap;
    min-height: 100vh;
    background-color: white;
    border-radius: 10px;
    border: solid 1px rgb(181, 179, 179);
  }

 .formbold-form-file-flex {
  display: flex;
  align-items: center;
  gap: 20px;
}
.formbold-form-file-flex .formbold-form-label {
  margin-bottom: 0;
}
.formbold-form-file {
  font-size: 14px;
  line-height: 24px;
  color: #536387;
}
.formbold-form-file::-webkit-file-upload-button {
  display: none;
}
.formbold-form-file:before {
  content: 'Upload file';
  display: inline-block;
  background: #EEEEEE;
  border: 0.5px solid #FBFBFB;
  box-shadow: inset 0px 0px 2px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  padding: 3px 12px;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  color: #637381;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  margin-right: 10px;
}


  // phone
  @media (max-width: 477px) {
    .title {
      p {
        margin-left: 60px;
      }
    }
    .allContentt {
      width: 370px !important;
    }
  
  }
  
  
  </style>
  